<template>
  <div>
    <mt-field
      label="用户名"
      placeholder="请输入用户名"
      v-model="username.text"

    ></mt-field>
    <mt-field
      type="password"
      label="密码"
      placeholder="请输入密码"
      v-model="password.text"
      
    ></mt-field>
    <mt-button type="default" @click.native="login">登录</mt-button>
  </div>
</template>

<style>
</style>

<script>
  export default {
    data () {
      return {
        username: {
          text: '',
          state: ''
        },
        password: {
          text: '',
          state: ''
        }
      }
    },
    methods: {
      login () {
        // this.axios({
        //   url: '/index.php/index/user/login',
        //   method: 'post',
        //   headers: {
        //     'Content-Type': 'application/x-www-form-urlencoded',
        //   },
        //   data: {
        //     name: this.username.text,
        //     password: this.password.text
        //   }
        // }).then(res => res.data)
        // .then(res => {
        //   console.log(res);
        // })
        this.$ajax({
          api: 'index/user/login',
          type: 'post',
          data: {
            name: this.username.text,
            password: this.password.text
          },
          success: data => {
            console.log(data)
          }
        })
      }
    }
  }
</script>


