import Vue from 'vue';
import VueRouter from 'vue-router';
import App from './App.vue';
import 'mint-ui/lib/style.css';
import mintUi from 'mint-ui';

import ajax from './libs/ajax.js';

Vue.use(ajax)

Vue.use(mintUi);
Vue.use(VueRouter);

import routes from './routes.js';
const router = new VueRouter({
  routes
});

const app = new Vue({
  router,
  render: h => h(App)
}).$mount('#app');
window.vm = app;



